.wp-block-create-block-button {
	color: #fff;
	padding: 2px;

	div {
		text-align: var(--btnAlign);

		.pagecrafter-btn {
			display: inline-block;
			padding: 10px 36px;
			background-color: #10b981;
			color: #fff;
			text-decoration: none;
			border: none;
			border-radius: 4px;
			cursor: pointer;
			transition: background-color 0.3s ease;
			font-size: 15px;
			// width: 100%;

		}
	}

}